<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">{{pageTitle}}</el-row>
    <el-row type="flex" justify="start" style="margin-top: 30px;">
     <el-form ref="form" :model="form" :rules="rules" label-width="80px" label-position="right">
        <el-form-item label="楼盘名称" prop="name">
          <el-input v-model="form.name" placeholder="请输入楼盘名称" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="封面图">
          <el-upload class="avatar-uploader"
                    action="http://szebra.cn/api/admin/images.json"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="form.imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
            <i class="avatar-limit">图片小于1M,格式jpg,png,jpeg</i>
        </el-form-item>
        <el-form-item label="均价">
          <el-input v-model="form.name" placeholder="请输入均价" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="楼盘地址">
          <el-input v-model="form.name" placeholder="请输入楼盘地址" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="咨询电话">
          <el-input v-model="form.name" placeholder="请输入资讯电话" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="开发商">
          <el-input v-model="form.name" placeholder="请输入开发商" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="物业公司">
          <el-input v-model="form.name" placeholder="请输入物业公司" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="开盘时间">
          <el-input v-model="form.name" placeholder="请输入开盘时间" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="交楼时间">
          <el-input v-model="form.name" placeholder="请输入交楼时间" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="物业类型">
          <el-input v-model="form.name" placeholder="请输入物业类型" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="产权年限">
          <el-input v-model="form.name" placeholder="请输入产权年限" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="车位数量">
          <el-input v-model="form.name" placeholder="请输入车位数量" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="装修标准">
          <el-input v-model="form.name" placeholder="请输入装修标准" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="绿化率">
          <el-input v-model="form.name" placeholder="请输入绿化率" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="容积率">
          <el-input v-model="form.name" placeholder="请输入容积率" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="关注基数">
          <el-input-number v-model="form.baseNum" @change="handleChange" label=""></el-input-number>
        </el-form-item>
        <el-form-item label="实景图" prop="image">
          <el-upload class="avatar-uploader"
                    action="http://szebra.cn/api/admin/images.json"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <img v-if="form.image" :src="form.image" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
            <i class="avatar-limit">图片小于1M,格式jpg,png,jpeg</i>
        </el-form-item>
        <el-form-item label="示意图" prop="image">
          <el-upload class="avatar-uploader"
                    action="http://szebra.cn/api/admin/images.json"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <img v-if="form.image" :src="form.image" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
            <i class="avatar-limit">图片小于1M,格式jpg,png,jpeg</i>
        </el-form-item>
        <el-form-item label="规划图" prop="image">
          <el-upload class="avatar-uploader"
                    action="http://szebra.cn/api/admin/images.json"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <img v-if="form.image" :src="form.image" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
            <i class="avatar-limit">图片小于1M,格式jpg,png,jpeg</i>
        </el-form-item>
        <el-form-item label="楼盘亮点">
          <el-input v-model="form.name" placeholder="请输入楼盘亮点" type="textarea" autosize></el-input>
        </el-form-item>
        <el-row style="margin-bottom: 30px;margin-top: 30px;">
          <el-button type="primary" plain size="small" @click="addText">添加亮点</el-button>
        </el-row>
        <el-button type="primary" @click="submitForm('form')" style="">保存</el-button>
      </el-form>
    </el-row>
  </div>
</template>
<script>

export default {
  components: {
  },

  data () {
    return {
      pageState: '', // 1新增，2编辑，3预览
      pageTitle: '', // 页面标题
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        baseNum: 0,
        delivery: false,
        type: [],
        resource: '',
        desc: '',
        radio: 1,
        imageUrl: ''
      },
      rules: { // 校验规则
        // name: { required: true, message: '请输入资讯标题名称', trigger: 'blur' }
      }
    }
  },

  watch: {
    pageState (newValue) {
      switch (newValue) {
        case 1:
          this.pageTitle = '新增楼盘'
          break
        case 2:
          this.pageTitle = '编辑楼盘'
          break
        case 3:
          this.pageTitle = '楼盘详情'
          break
      }
    }
  },

  methods: {
    // 提交表单
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          return false
        }
      })
    }
  },

  mounted () {
    this.pageState = this.$route.query.pageState || 1
  }
}
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .avatar-limit{
    font-size: 12px;
  }
</style>
